<template>
	<div class="input-wrap">
		<input type="text" v-model="inputValue" class="input-cls" :placeholder="placeholder" @blur="inputShowClear(1)" @focus="inputShowClear(2)" @input="inputChange($event, name)">
		<i class="clear icon icon-dismiss" v-show="showClear" @click="clearInputValue(name)" v-if="isShowClear"></i>
	</div>
</template>

<script>
export default {
	props: {
		'isShowClear': {
			type: Boolean,
			default: true
		},
		'selectValue': {
			type: String
		},
		'placeholder': {
			type: String,
			default: ''
		},
		'name': {
			type: String
		},
		trim: {
			type: Boolean,
			default: false
		},
		inputVal: String
	},
	name: 'InputComponent',
	data () {
		return {
			showClear: 0,
			inputValue: this.inputVal
		}
	},
	watch: {
		inputVal(newVal) {
			this.inputValue = newVal
		}
	},
	methods: {
		inputShowClear (type) {
			let isShow = 0
			if (type === 2) {
				this.$emit('inputFocus')
				isShow = this.inputValue ? 1 : 0
			} else {
				this.$emit('inputBlur')
			}
			setTimeout(() => {
				this.showClear = isShow
			}, 10)
		},
		inputChange (e, name) {
			let sValue = e.target.value.replace(/\s+/g, '')
			clearTimeout(this.timer)
			this.timer = setTimeout(() => {
				this.inputValue = sValue
				this.showClear = sValue ? 1 : 0
				this.$emit('inputHandler', {
					name,
					value: this.inputValue
				})
			}, 100)
		},
		clearInputValue (name) {
			this.inputValue = ''
			this.showClear = 0
			this.$emit('inputClearHandler', name)
		}
	},
	mounted () {
		if (this.trim) {
			this.$watch('inputValue', value => {
				const trimValue = value.trim()
				this.inputValue = trimValue
			})
		}
	}
}
</script>

<style lang="scss" scoped>
	.input-wrap{
		display: flex;
		align-items: center;
		.input-cls {
			width: 100%;
			flex: 1;
			height: 50px;
			font-size: 16px;
			background: transparent;
		}

		i {
			width: 30px;
			color: #BDBDBD;
			text-align: center;
			&.clear{
				height: 40px;
				line-height: 40px;
				font-size: 16px;
			}
		}
	}

</style>



