<template>
  <transition name="slide">
    <div class="user-wrap">
      <i class="iconfont icon-back icon-fanhui" @click="$router.back()"></i>
      <div class="switches-box">登录</div>
      <div class="form">
        <div class="input-wrapper">
          <AppInput class="login-input" placeholder="输入手机号或邮箱" @inputHandler="changePhone" :inputVal="phoneNumber"
                @inputClearHandler="changePhone({value: ''})"/>
        </div>
        <div class="input-wrapper">
          <AppInput class="login-input" placeholder="输入验证码" @inputHandler="changeValidateCode" :inputVal="validateCode"
                @inputClearHandler="changeValidateCode({value:''})"/>
          <div id="split-line"></div>
          <div id="code-sender" :class="{ disable: disableSendValidateBtn }" @click="requestValidateCode">{{
            senderText }}<span id="countdown" v-if="countDown > 0">({{ countDown }})</span></div>
        </div>
        <AppButton ref="submit" id="login-button" :btns="buttons" :class="{ disable: disableSubmit }"/>
        <!-- <div class="switch-text" @click="switchLoginRegistry">
          <span>没有账号？去注册</span>
        </div> -->
      </div>
      <ul class="third-login">
        <li>
          <img src="~@/assets/QQ.png" alt="">
          <div class="text">QQ登录</div>
        </li>
        <li>
          <img src="~@/assets/weixin.png" alt="">
          <div class="text">微信登录</div>
        </li>
        <li>
          <img src="~@/assets/weibo.png" alt="">
          <div class="text">微博登录</div>
        </li>
      </ul>
    </div>
  </transition>
</template>

<script>
import AppInput from '@/base/input'
import AppButton from '@/base/button'
import { mapActions } from 'vuex'
  export default {
    data() {
      const buttons = [
        {
          text: '登录',
          bgColor: '#F24E5A',
          eventHandler: this.submit
        }
      ]
      return {
        buttons,
        phoneNumber: '',
        validateCode: '',
        sent: false,
        disableSubmit: true,
        countDown: 0,
        disableSendValidateBtn: false
      }
    },
    components: {
      AppInput,
      AppButton
    },
    computed: {
      senderText () {
        if (!this.sent) {
          return '获取验证码'
        } else {
          return '重新发送'
        }
      }
    },
    activated() {
      this.phoneNumber = ''
      this.validateCode = ''
      this.sent = false
      this.disableSubmit = true
      this.countDown = 0
      this.disableSendValidateBtn = false
    },
    methods: {
      submit () {
        if (this.phoneNumber && this.validateCode) {
          this.setLoginStatus(true)
          this.$router.replace('/home')
        } else {
          this.$toast({
            content: '请输入手机号和密码'
          })
        }
      },
      changePhone ({ value }) {
        this.phoneNumber = value
        this.checkBtnStatus()
      },
      checkBtnStatus () {
        if (this.phoneNumber && this.validateCode && this.sent) {
          this.disableSubmit = false
        } else {
          this.disableSubmit = true
        }
      },
      changeValidateCode ({ value }) {
        this.validateCode = value
        this.checkBtnStatus()
      },
      requestValidateCode () {
        if (!this.disableSendValidateBtn) {
          this.disableSendValidateBtn = true
          this.sent = true
          this.setClock()
        }
      },
      setClock () {
        this.countDown = 60
        const timer = setInterval(() => {
          this.countDown--
          if (this.countDown === 0) {
            clearTimeout(timer)
            this.disableSendValidateBtn = false
          }
        }, 1000)
      },
      switchLoginRegistry() {

      },
      ...mapActions(['setLoginStatus'])
    }
  }
</script>
<style lang='scss' scoped>
  @import "../../common/scss/variable";
  .user-wrap {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background: #f5f5f5;
    &.slide-enter-active, &.slide-leave-active {
      transition: all .3s;
    }
    &.slide-enter, &.slide-leave-to {
      transform: translate3d(100%, 0, 0);
    }
    .icon-back {
      position: absolute;
      top: 0;
      left: 6px;
      padding: 10px;
      font-size: $font-size-large;
      color: $color-theme;
    }
    .switches-box {
      height: 45px;
      line-height: 45px;
      text-align: center;
      border-bottom: 1px solid #f5f5f5;
      background: $color-background;
    }
  }
  .form {
    padding: 50px 30px 0;
  }
.input-wrapper {
	margin-top: 10px;
	display: flex;
	align-items: center;
  border-bottom: 1px solid #ebe8e8;
}
.login-input {
	flex-grow: 1;
}
#code-sender {
	font-size: 14px;
	color: #397bb9;

	&.disable {
		color: #999;
	}
}
.switch-text {
  text-align: right;
  font-size: 14px;
  color: #666;
  span {
    text-decoration: underline;
  }
}
.third-login {
  display: flex;
  position: absolute;
  bottom: 40px;
  left: 0;
  right: 0;
  text-align: center;
  justify-content: center;
  li {
    padding: 0 20px;
  }
  li img {
    width: 28px;
  }
  .text {
    font-size: 12px;
    padding-top: 6px;
    color: #666;
  }
}
</style>



