<template>
	<div class="btn-group clearfix">
		<template v-for="(item, index) in btns">
			<button class="submit-btn" @click="item.eventHandler" :key="index" :style="{'background-color': item.bgColor}"
					:class="{'cancel': item.style=='default', 'disabled': item.disabled}">
				{{item.text}}
			</button>
		</template>
	</div>
</template>

<script>
export default {
	name: 'index',
	data () {
		return {}
	},
	props: {
		btns: {
			type: Array,
			default: () => []
		}
		}
}
</script>

<style lang="scss" scoped>
.btn-group {
	margin: 30px 0 10px;
	display: flex;
	justify-content: space-around;
	align-items: center;
	
	.submit-btn {
		flex: 1;
		margin: 15px auto;
		height: 44px;
		color: #fff;
		background: #397BB9;
		border-radius: 44px;
		font-size: 16px;
	}
	
	.cancel {
		float: right;
		background: #fff;
		color: #4A4A4A;
		border: 1px solid rgba(220, 220, 220, 1);
		margin-left: 30px;
	}
	
	.submit-btn.disabled {
		pointer-events: none;
		opacity: .35;
	}
}
</style>



